<template>
  <div class="main-div">
      <b-row class="mt-2">
        <b-col cols="2" class="col-left">
          <label>Employee Loan Summary</label>
        </b-col>
        <b-col cols="2" offset="6" class="col-left">
          <label>Payroll Summary</label>
        </b-col>
      </b-row>
      <b-row class="mt-2">
        <b-col cols="2" class="col-left">Total Loans:</b-col>
        <b-col cols="2" class="col-right">
           <vue-numeric
            class="form-control form-control-sm styleLine col-right"
            style="padding-left: 0px"
            tabindex="74"
            placeholder
            separator=","
            :precision="3"
            v-model="empLoanSummary.drAmountTotal"
          ></vue-numeric>
        </b-col>
        <b-col cols="2" class="col-left">Loan Installment</b-col>
        <b-col cols="2" class="col-right">
           <vue-numeric
            class="form-control form-control-sm styleLine col-right"
            style="padding-left: 0px"
            tabindex="74"
            placeholder
            separator=","
            :precision="3"
            v-model="empLoanSummary.loanInstallment"
          ></vue-numeric>
        </b-col>
        <b-col cols="2" class="col-left">Indeminity</b-col>
        <b-col cols="2" class="col-right">
           <vue-numeric
            class="form-control form-control-sm styleLine col-right"
            style="padding-left: 0px"
            tabindex="74"
            placeholder
            separator=","
            :precision="3"
            v-model="empLoanSummary.indeminity"
          ></vue-numeric>
        </b-col>
      </b-row>
      <b-row class="mt-2">
        <b-col cols="2" class="col-left">Total Paid</b-col>
        <b-col cols="2" class="col-right">
           <template>
          <vue-numeric
            class="form-control form-control-sm styleLine col-right"
            style="padding-left: 0px"
            tabindex="74"
            placeholder
            separator=","
            :precision="3"
            v-model="empLoanSummary.crAmountTotal"
          ></vue-numeric>
        </template>
        </b-col>
        <b-col cols="2" class="col-left">Additional Installment</b-col>
        <b-col cols="2">
           <vue-numeric
            class="form-control form-control-sm styleLine col-right"
            style="padding-left: 0px"
            tabindex="74"
            placeholder
            separator=","
            :precision="3"
            v-model="empLoanSummary.additionalInstallment"
          ></vue-numeric>
        </b-col>
        <b-col cols="2" class="col-left">Basic Salary</b-col>
        <b-col cols="2" class="col-right">
           <vue-numeric
            class="form-control form-control-sm styleLine col-right"
            style="padding-left: 0px"
            tabindex="74"
            placeholder
            separator=","
            :precision="3"
            v-model="empLoanSummary.basicSalary"
          ></vue-numeric>
        </b-col>
      </b-row>
      <b-row class="mt-1">
        <b-col cols="2" class="col-left">Last transaction Date</b-col>
        <b-col cols="2" class="col-left">
          <b-form-input
            class="col-right"
            :readonly="true"
            v-model="empLoanSummary.lastTrnDate"
          ></b-form-input>
        </b-col>
        <b-col cols="2" class="col-left">Total Installment</b-col>
        <b-col cols="2" class="col-right">
          {{ this.totalInstallment.toFixed(3) }}
        </b-col>
      </b-row>

  </div>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  data () {
    return {
      indeminity: '5500',
      basicSalary: '1800'
    }
  },
  computed: {
    ...mapGetters(['empLoanSummary']),
    totalInstallment: function () {
      return parseFloat(this.empLoanSummary.loanInstallment || 0) + parseFloat(this.empLoanSummary.additionalInstallment || 0)
    }
  },
  methods: {
    // ...mapActions([''])
  },
  mounted () {
    // console.log('mounted loan summary')
  }
}
</script>
<style  scoped>
.main-div {
  width: 95%;
  margin-left: 40px;
  margin-right: 40px;
}
.no-border {
  border: 0;
  box-shadow: none; /* You may want to include this as bootstrap applies these styles too */
}

.col-left {
  text-align: left;
}
.col-right {
  text-align: right;
}
</style>
